import { collectVisualStyleFromProps } from "../style/collectVisualStyleFromProps"

export function upgradeProps(props: any, component = "Frame"): any {
    if (!props) return props
    if (props._border || props.constraints) return props
    if (component === "Vector") return props
    if (component === "Text") return props

    const {
        overflow,
        rotation,
        opacity,
        left,
        right,
        top,
        bottom,
        centerX,
        centerY,
        width,
        height,
        aspectRatio,
        parentSize,
        borderWidth,
        borderStyle,
        borderColor,
        ...newProps
    } = props

    const style: any = props.style || {}

    // This function is supposed to move to the Vekter side, but is now used to provide a large
    // part of the compatibility. If we change this function, library will become less compatible.
    collectVisualStyleFromProps(props, style)

    if (overflow !== undefined) {
        style.overflow = props.overflow
    }
    if (rotation !== undefined) {
        style.rotate = props.rotation + "deg"
    }
    if (opacity !== undefined && opacity !== 1) {
        style.opacity = opacity
    }
    newProps.style = style

    const _border: any = {}
    if (borderWidth) {
        _border.borderWidth = borderWidth
    }
    if (borderStyle) {
        _border.borderStyle = borderStyle
    }
    if (borderColor) {
        _border.borderColor = borderColor
    }
    newProps._border = _border

    const constraints = { left, right, top, bottom, centerX, centerY, width, height, parentSize, aspectRatio }
    newProps.constraints = constraints
    return newProps
}
