import * as React from "react"
import { MotionStyle } from "framer-motion"

import { StackSpecificProps, StackAlignment, StackDistribution, StackDirection } from "./types"
import { FrameWithMotion, FrameProps, isFiniteNumber, ConstraintProperties, ConstraintsContext } from "../../render"
import { isReactChild, isReactElement } from "../../utils/type-guards"
import { paddingFromProps } from "../utils/paddingFromProps"

import { injectStackCSSRules } from "./injectStackCSSRules"
/**
 * The Stack component takes the same props as the {@link Frame} component as well as a few
 * additional interface defined below.
 * @public
 */
export interface StackProperties extends StackSpecificProps, FrameProps {
    children: React.ReactNode
}

type FlexDirection = "column" | "row" | "column-reverse" | "row-reverse"
type LayoutBox = {
    left: number
    top: number
    bottom: number
    right: number
}

/**
 * @beta
 */
export function Stack({
    direction = "vertical",
    distribution = "start",
    alignment = "center",
    gap = 10,
    children,
    style: styleProp,
    __fromCodeComponentNode,
    ...rest
}: Partial<StackProperties>) {
    injectStackCSSRules()
    const flexDirection = toFlexDirection(direction)
    const isReverse = isReverseDirection(flexDirection)

    const style: MotionStyle = {
        display: "flex",
        flexDirection,
        justifyContent: toJustifyOrAlignment(distribution),
        alignItems: toJustifyOrAlignment(alignment),
        padding: makePaddingString(paddingFromProps(rest)),
        ...styleProp,
    }

    // const parentSize = useContext(parentSizeContext)
    // const useConstraints = !!parentSize && rest.constraints && rest.constraints.enabled !== false

    const childCount = React.Children.count(children)
    let background = "none"
    if (!__fromCodeComponentNode && childCount === 0) {
        background = "rgba(0, 170, 255, 0.3)"
    }
    return (
        <FrameWithMotion
            background={background} // need to set here to prevent default background from Frame
            {...rest}
            data-framer-stack={true}
            data-framer-stack-direction-reverse={isReverse}
            style={style}
        >
            <ConstraintsContext.Provider value={{ size: undefined }}>
                {wrapInGapElement(handleFraction(children, direction), gap, flexDirection)}
            </ConstraintsContext.Provider>
        </FrameWithMotion>
    )
}

/**
 * Private helper function
 */
function isFractionDimension(dimension: any): dimension is string {
    return typeof dimension === "string" && dimension.endsWith("fr")
}

function fraction(dimension: string) {
    const value = parseFloat(dimension)
    return isFiniteNumber(value) ? value : 0
}

function handleFraction(children: React.ReactNode, direction: StackDirection) {
    return React.Children.map(children, child => {
        if (!isReactChild(child) || !isReactElement(child)) return
        const isVertical = direction === "vertical"
        const style: MotionStyle = {}
        let hasFraction = false
        const { constraints, style: propsStyle, width, height } = child.props as Partial<{
            width?: number | string
            height?: number | string
            constraints?: Partial<ConstraintProperties>
            style?: MotionStyle
        }>

        let currentWidth: any = width
        // We're moving away from the constraints prop, this is legacy support
        if (currentWidth !== undefined && constraints) {
            currentWidth = constraints.width
        }
        let currentHeight: any = height
        // We're moving away from the constraints prop, this is legacy support
        if (currentHeight !== undefined && constraints) {
            currentHeight = constraints.height
        }

        if (isFractionDimension(currentWidth)) {
            hasFraction = true
            hasFraction = true
            if (isVertical) {
                style.width = `${fraction(currentWidth) * 100}%`
            } else {
                style.width = 1
                style.flexGrow = fraction(currentWidth)
                style.flexBasis = 0
            }
        }

        if (isFractionDimension(currentHeight)) {
            hasFraction = true
            if (isVertical) {
                style.height = 1
                style.flexGrow = fraction(currentHeight)
                style.flexBasis = 0
            } else {
                style.height = `${fraction(currentHeight) * 100}%`
            }
        }

        if (!hasFraction) return child

        const nextStyle = { ...propsStyle, ...style }
        return React.cloneElement(child, { ...child.props, style: nextStyle } as React.BaseHTMLAttributes<
            HTMLDivElement
        >)
    })
}

function wrapInGapElement(children: React.ReactNode, gap: number | undefined, direction: FlexDirection) {
    if (!gap) return children

    const isVertical = isVerticalDirection(direction)

    const gapStyle: React.CSSProperties = {
        display: "contents",
        ["--stack-gap-x" as any]: `${isVertical ? 0 : gap}px`,
        ["--stack-gap-y" as any]: `${isVertical ? gap : 0}px`,
    }

    return (
        <div data-framer-stack-gap={true} style={gapStyle}>
            {children}
        </div>
    )
}

function makePaddingString({ top, left, bottom, right }: LayoutBox) {
    return `${top}px ${right}px ${bottom}px ${left}px`
}

function toFlexDirection(direction: StackDirection): FlexDirection {
    switch (direction) {
        case "vertical":
            return "column"
        case "horizontal":
            return "row"
        default:
            return direction
    }
}

function isVerticalDirection(direction: FlexDirection) {
    return direction === "column" || direction === "column-reverse"
}

function isReverseDirection(direction: FlexDirection) {
    switch (direction) {
        case "column-reverse":
        case "row-reverse":
            return true
        default:
            return false
    }
}

function toJustifyOrAlignment(distribution: StackDistribution | StackAlignment) {
    switch (distribution) {
        case "start":
            return "flex-start"
        case "end":
            return "flex-end"
        default:
            return distribution
    }
}
