import * as React from "react"
import {
    isConstraintSupportingClass,
    isConstraintSupportingChild,
    getMergedConstraintsProps,
} from "../types/Constraints"
import { WithParentSize } from "./PresentationTree"
import { Size } from "../types/Size"
import { ConstraintsContext } from "./Frame"
const hoistNonReactStatic = require("hoist-non-react-statics")

/**
 * @internal
 */
export interface WithSize {
    width?: number
    height?: number
}

function getDisplayName(WrappedComponent: React.ComponentType) {
    return WrappedComponent.displayName || WrappedComponent.name || "Component"
}

/**
 * @internal
 */
export function SetSizeAndPositionChildren<TOriginalProps extends WithSize, BaseProps extends TOriginalProps>(
    BaseComponent: React.ComponentType<BaseProps>
): React.ComponentClass<BaseProps & WithParentSize> {
    type Props = BaseProps & WithParentSize

    const baseComponentSupportsConstraints = isConstraintSupportingClass(BaseComponent)

    class Unwrap extends React.Component<Props, {}> {
        static displayName = `WithSizeAndPositionChildren(${getDisplayName(BaseComponent)})`
        static supportsConstraints = true

        // Not hoisted by `hoistNonReactStatic`
        static defaultProps = Object.assign({}, BaseComponent.defaultProps) as Partial<Props>

        render() {
            return (
                <ConstraintsContext.Consumer>
                    {({ size: parentSize }) => {
                        const children = React.Children.map(this.props.children, (child: React.ReactChild) => {
                            if (isConstraintSupportingChild(child)) {
                                const constraints = {
                                    left: 0,
                                    top: 0,
                                    bottom: null,
                                    right: null,
                                }
                                const constraintProps = getMergedConstraintsProps(child.props, constraints)
                                return React.cloneElement(child, constraintProps)
                            }
                            return child
                        })
                        const size: Partial<Size> = {}
                        const props = this.props

                        if (!baseComponentSupportsConstraints) {
                            if (parentSize) {
                                size.width = parentSize.width
                                size.height = parentSize.height
                            }
                        }
                        return (
                            <BaseComponent {...props} {...size}>
                                {children}
                            </BaseComponent>
                        )
                    }}
                </ConstraintsContext.Consumer>
            )
        }
    }
    hoistNonReactStatic(Unwrap, BaseComponent)
    return Unwrap
}
