import * as React from "react"
import { useMemo, useRef, useCallback } from "react"
import {
    useMotionValue,
    DraggableProps,
    MotionValue,
    AnimationControls,
    unwrapMotionValue,
    PanInfo,
} from "framer-motion"
import { useElementSize } from "./useElementSize"

import { isRectProviding } from "../utils/RectProvider"
import { Rect, Size, ConstraintProperties, ControlType, Point, debounce } from "../../render"
import {
    FrameWithMotion,
    FrameProps,
    useConstraints,
    defaultFrameRect,
} from "../../render/presentation/Frame/FrameWithMotion"
import { clamp } from "../../utils/math"
import { Omit } from "../../utils/omit"
import { EmptyState } from "../../components/EmptyState"
import { addPropertyControls } from "../../utils/addPropertyControls"
import { isMotionValue } from "../../render/utils/isMotionValue"
import { RenderEnvironment, RenderTarget } from "../../render/types/RenderEnvironment"

export type ScrollInfo = {
    offset: Point
    velocity: Point
}

const directionMap: { [key: string]: DraggableProps["drag"] } = {
    horizontal: "x",
    vertical: "y",
    both: true,
}

function convertScrollDirectionToDrag(scrollDirection?: "horizontal" | "vertical" | "both") {
    return scrollDirection ? directionMap[scrollDirection] : scrollDirection
}

/**
 * @public
 */
export interface ScrollEvents {
    /**
     * Called when scrolling starts.
     *
     * @remarks
     * ```jsx
     * function onScrollStart(info) {
     *   console.log(info.offset, info.velocity)
     * }
     *
     * <Scroll onScrollStart={onScrollStart} />
     * ```
     * @param info - An {@link PanInfo} object containing `x` and `y` values for:
     *
     *   - `point`: Relative to the device or page.
     *   - `delta`: Distance moved since the last event.
     *   - `offset`: Offset from the original pan event.
     *   - `velocity`: Current velocity of the pointer.
     * @public
     */
    onScrollStart?(info: PanInfo): void

    /**
     * Called periodically during scrolling.
     *
     * @remarks
     * ```jsx
     * function onScroll(info) {
     *   console.log(info.offset, info.velocity)
     * }
     *
     * <Scroll onScroll={onScroll} />
     * ```
     * @param info - An {@link PanInfo} object containing `x` and `y` values for:
     *
     *   - `point`: Relative to the device or page.
     *   - `delta`: Distance moved since the last event.
     *   - `offset`: Offset from the original pan event.
     *   - `velocity`: Current velocity of the pointer.
     * @public
     */
    onScroll?(info: PanInfo): void
    /**
     * Called periodically during scrolling.
     *
     * @remarks
     * ```jsx
     * function onScrollEnd(info) {
     *   console.log(info.offset, info.velocity)
     * }
     *
     * <Scroll onScrollEnd={onScrollEnd} />
     * ```
     * @param info - An {@link PanInfo} object containing `x` and `y` values for:
     *
     *   - `point`: Relative to the device or page.
     *   - `delta`: Distance moved since the last event.
     *   - `offset`: Offset from the original pan event.
     *   - `velocity`: Current velocity of the pointer.
     * @public
     */
    onScrollEnd?(info: PanInfo): void
}

/**
 * The properties for the {@link Scroll} component, which are also available within other components, like {@link Page}.
 * @public
 */
export interface ScrollConfig {
    /**
     * Controls the axis of drag-scrolling.
     * Defaults to `"vertical"` for vertical scrolling.
     *
     * @remarks
     * Set `"horizontal"` or `"vertical"` to only drag in a specific direction.
     * Set `"both"` to drag both directions.
     *
     * ```jsx
     * // Horizontal
     * <Scroll direction="horizontal" />
     *
     * // Vertical
     * <Scroll direction="vertical" />
     *
     * // Locked
     * <Scroll direction="locked" />
     *
     * // Both directions
     * <Scroll direction="both" />
     * ```
     *
     * @public
     */
    direction?: "horizontal" | "vertical" | "both"

    /**
     * If `true`, this will lock dragging to the initial direction.
     *
     * @public
     *
     * ```jsx
     * <Scroll direction="both" directionLock={true} />
     * ```
     */
    directionLock?: boolean

    /**
     * Enable or disable dragging to scroll. Defaults to `true`.
     *
     * @public
     *
     * ```jsx
     * <Scroll dragEnabled={false} />
     * ```
     */
    dragEnabled?: boolean

    /**
     * Enable or disable wheel scroll. Defaults to `true`.
     *
     * @public
     *
     * ```jsx
     * <Scroll wheelEnabled={false} />
     * ```
     */
    wheelEnabled?: boolean

    /**
     * Horizontal offset of the scrollable content. Set to `0` by default
     *
     * @remarks
     * ```jsx
     * <Scroll contentOffsetX={20} />
     * ```
     */
    contentOffsetX?: MotionValue<number> | number

    /**
     * Vertical offset of the scrollable content. Set to `0` by default.
     *
     * @remarks
     * ```jsx
     * <Scroll contentOffsetY={20} />
     * ```
     */
    contentOffsetY?: MotionValue<number> | number

    /**
     * Width of the scrollable content.
     *
     * @remarks
     * ```jsx
     * <Scroll contentWidth={500} />
     * ```
     */
    contentWidth?: number

    /**
     * Height of the scrollable content.
     *
     * @remarks
     * ```jsx
     * <Scroll contentHeight={500} />
     * ```
     */
    contentHeight?: number

    /**
     * Add a custom control for the scroll animation.
     * @remarks
     * ```jsx
     * const animation = useAnimation()
     * animation.start({y: -50})
     * <Scroll scrollAnimation={animation} />
     * ```
     * @beta
     * */
    scrollAnimation?: AnimationControls

    /**
     * @internalremark If `true`, specifies that the component is a direct child of a ComponentContainer, rendered by a CodeComponentNode that is placed on the canvas.
     * @internal
     * */
    __fromCodeComponentNode?: boolean
}

/**
 * @public
 */
export interface ScrollProps extends Omit<Partial<FrameProps>, "onScroll" | "size">, ScrollEvents, ScrollConfig {}

type Constraints = {
    top: number
    left: number
    right: number
    bottom: number
}

const useScrollConstraints = (
    rect: Size | null,
    dragDirection: "both" | "horizontal" | "vertical" | "locked",
    children: React.ReactNode
) => {
    return useMemo((): [Size, React.ReactNode | React.ReactChild[]] => {
        const contentSize: Size = {
            width: 0,
            height: 0,
        }

        if (!rect) return [contentSize, children]
        contentSize.width = rect.width
        contentSize.height = rect.height

        const resizedChildren = React.Children.map(children, (child: React.ReactChild) => {
            if (child === null || typeof child !== "object" || typeof child.type === "string") {
                return child
            }

            const type = child.type
            if (isRectProviding(type)) {
                const frame = type.rect(child.props, rect)
                contentSize.width = Math.max(Rect.maxX(frame), contentSize.width)
                contentSize.height = Math.max(Rect.maxY(frame), contentSize.height)
            }
            if (child.props.constraints) {
                const update: { constraints: Partial<ConstraintProperties> } = {
                    constraints: { ...child.props.constraints },
                }
                if (dragDirection === "vertical") {
                    update.constraints.width = rect.width
                } else if (dragDirection === "horizontal") {
                    update.constraints.height = rect.height
                }

                return React.cloneElement(child, update)
            } else {
                // Old constraints API
                const update: Partial<{ width: number; height: number }> = {}
                if (dragDirection === "vertical") {
                    update.width = rect.width
                } else if (dragDirection === "horizontal") {
                    update.height = rect.height
                }

                return React.cloneElement(child, update)
            }
        })
        return [contentSize, resizedChildren]
    }, [rect, dragDirection, children])
}

/**
 * @public
 */
export function Scroll(props: ScrollProps) {
    const {
        direction = "vertical",
        directionLock = false,
        dragEnabled: dragEnabled = true,
        wheelEnabled = true,
        contentOffsetX = 0,
        contentOffsetY = 0,
        contentWidth,
        contentHeight,
        onScrollStart,
        onScroll,
        onScrollEnd,
        onDragStart,
        onDrag,
        onDragEnd,
        onUpdate,
        onDirectionLock,
        style,
        children,
        scrollAnimation,
        overflow = "hidden",
        ...containerProps
    } = props
    let _constraints = containerProps.constraints
    const defaultX = useMotionValue(typeof contentOffsetX === "number" ? contentOffsetX : 0)
    const defaultY = useMotionValue(typeof contentOffsetY === "number" ? contentOffsetY : 0)
    const x = isMotionValue(contentOffsetX) ? contentOffsetX : defaultX
    const y = isMotionValue(contentOffsetY) ? contentOffsetY : defaultY
    const { initial, prev } = useRef({
        initial: { x: 0, y: 0 },
        prev: { x: 0, y: 0 },
    }).current

    if (containerProps.__fromCodeComponentNode && !_constraints) {
        _constraints = { width: "100%", height: "100%" }
    }
    const size = useConstraints(_constraints)
    const [contentSize, resizedChildren] = useScrollConstraints(size, direction, children)
    const { target } = RenderEnvironment

    const isPreview = target === RenderTarget.preview

    const dragRef = useRef<HTMLDivElement>(null)
    const hasFixedContentSize = contentWidth !== undefined && contentHeight !== undefined

    let actualContentSize = useElementSize(dragRef, isPreview, hasFixedContentSize || !isPreview, contentSize)

    if (contentWidth !== undefined) {
        actualContentSize = { ...actualContentSize, width: contentWidth }
    }

    if (contentHeight !== undefined) {
        actualContentSize = { ...actualContentSize, height: contentHeight }
    }

    const dragConstraints: Constraints = {
        top: -actualContentSize.height,
        left: -actualContentSize.width,
        right: 0,
        bottom: 0,
    }

    if (size) {
        dragConstraints.top += size.height
        dragConstraints.left += size.width
    } else {
        if (containerProps.width === undefined) {
            dragConstraints.left += defaultFrameRect.width
        } else if (typeof containerProps.width === "number") {
            dragConstraints.left += containerProps.width
        }
        if (containerProps.height === undefined) {
            // here should be scroll area height
            dragConstraints.top += defaultFrameRect.height
        } else if (typeof containerProps.height === "number") {
            dragConstraints.top += containerProps.height
        }
    }

    dragConstraints.top = Math.min(dragConstraints.top, 0)
    dragConstraints.left = Math.min(dragConstraints.left, 0)

    const getLatestPoint = () => ({ x: x.get(), y: y.get() })

    const resetInitialPoint = useCallback(() => {
        const point = getLatestPoint()
        initial.x = point.x
        initial.y = point.y
        prev.x = point.x
        prev.y = point.y
    }, [])

    const getPointData = useCallback(() => {
        const point = getLatestPoint()

        const data = {
            point,
            velocity: { x: x.getVelocity(), y: y.getVelocity() },
            offset: { x: point.x - initial.x, y: point.y - initial.y },
            delta: { x: point.x - prev.x, y: point.y - prev.y },
        }

        prev.x = point.x
        prev.y = point.y

        return data
    }, [x, y])

    const updateScrollListeners = useCallback(
        values => {
            onUpdate && onUpdate(values)
            onScroll && onScroll(getPointData())
        },
        [x, y]
    )

    const isWheelScrollActive = React.useRef(false)
    const handleWheel = useMemo(() => {
        function clampX(v: number) {
            return clamp(v, dragConstraints.left, dragConstraints.right)
        }

        function clampY(v: number) {
            return clamp(v, dragConstraints.top, dragConstraints.bottom)
        }

        function updateX(delta: number) {
            x.stop()
            x.set(clampX(x.get() - delta))
        }

        function updateY(delta: number) {
            y.stop()
            y.set(clampY(y.get() - delta))
        }

        const debouncedOnScrollEnd = debounce(() => {
            onScrollEnd && onScrollEnd(getPointData())
            isWheelScrollActive.current = false
        }, 200)

        return (e: React.WheelEvent) => {
            e.preventDefault()

            if (!wheelEnabled) return

            if (!isWheelScrollActive.current) {
                resetInitialPoint()
                onScrollStart && onScrollStart(getPointData())
                isWheelScrollActive.current = true
            }

            switch (direction) {
                case "horizontal":
                    updateX(e.deltaX)
                    break
                case "vertical":
                    updateY(e.deltaY)
                    break
                default:
                    updateX(e.deltaX)
                    updateY(e.deltaY)
            }

            debouncedOnScrollEnd()
        }
    }, [x, y, direction, dragConstraints])

    return (
        <FrameWithMotion
            onWheel={handleWheel}
            {...containerProps}
            overflow={overflow}
            constraints={_constraints}
            style={{ background: "transparent", ...style }}
            preserve3d={containerProps.preserve3d}
        >
            <FrameWithMotion
                animate={scrollAnimation}
                drag={dragEnabled && convertScrollDirectionToDrag(direction)}
                dragDirectionLock={directionLock}
                dragConstraints={dragConstraints}
                onDragStart={(event, info) => {
                    resetInitialPoint()
                    onDragStart && onDragStart(event, info)
                    onScrollStart && onScrollStart(info)
                }}
                onDrag={onDrag}
                onDragEnd={onDragEnd}
                onDragTransitionEnd={() => onScrollEnd && onScrollEnd(getPointData())}
                onUpdate={updateScrollListeners}
                onDirectionLock={onDirectionLock}
                width={actualContentSize.width}
                height={actualContentSize.height}
                style={{
                    background: "transparent",
                    overflow: "visible",
                    x,
                    y,
                }}
                preserve3d={containerProps.preserve3d}
            >
                {/* We need to wrap content within a div to make resize observer work */}
                <div ref={dragRef} style={{ display: "contain" }}>
                    <EmptyState
                        children={children!}
                        size={{
                            width:
                                (size && size.width) ||
                                unwrapMotionValue(containerProps.width) ||
                                defaultFrameRect.width,
                            height:
                                (size && size.height) ||
                                unwrapMotionValue(containerProps.height) ||
                                defaultFrameRect.height,
                        }}
                        insideUserCodeComponent={!containerProps.__fromCodeComponentNode}
                        title={"Connect to scrollable content"}
                    />
                    {resizedChildren}
                </div>
            </FrameWithMotion>
        </FrameWithMotion>
    )
}

addPropertyControls(Scroll, {
    direction: {
        type: ControlType.SegmentedEnum,
        title: "Direction",
        options: ["vertical", "horizontal", "both"],
        defaultValue: "vertical",
    },
    directionLock: {
        type: ControlType.Boolean,
        title: "Lock",
        enabledTitle: "1 Axis",
        disabledTitle: "Off",
        defaultValue: true,
    },
})
;(Scroll as any).supportsConstraints = true
;(Scroll as any)._isStylable = true
