export interface BackgroundImageState {
    shouldCheckImageAvailability: boolean
    currentBackgroundImageSrc: string | null
}

export const ConstraintsContext = React.createContext<{ size?: Size }>({ size: undefined })

export function calcConstraints(constraints?: Partial<ConstraintProperties>, parentSize?: Size) {
    if (!constraints) return null
    const constraintValues = ConstraintValues.fromProperties({ ...constraintDefaults, ...constraints })
    const rect = ConstraintValues.toRect(constraintValues, parentSize || null, null, true)
    return rect
}

export function useConstraints(constraints?: Partial<ConstraintProperties>): Rect | null {
    const { size: parentSize } = React.useContext(ConstraintsContext)
    return calcConstraints(constraints, parentSize)
}

function useBackgroundImage(
    props: Partial<BackgroundProperties>,
    rect: Rect | null,
    ref: React.RefObject<HTMLElement | null>
): React.CSSProperties {
    let backgroundImage: BackgroundImage | undefined
    if (BackgroundImage.isImageObject(props.background)) {
        backgroundImage = props.background
    }
    const nextBackgroundImageSrc = props.background && backgroundImage ? backgroundImage.src : null
    const [backgroundImageState, setBackgroundImageState] = useState<BackgroundImageState>({
        shouldCheckImageAvailability: true,
        currentBackgroundImageSrc: null,
    })
    if (!backgroundImage) return {}
    const style = {}
    if (nextBackgroundImageSrc) {
        if (rect === null) {
            setImageForFill(props.background as BackgroundImage, null, style)
            return style
        }
        const shouldCheckImageAvailability = backgroundImageState.currentBackgroundImageSrc !== nextBackgroundImageSrc
        if (shouldCheckImageAvailability !== backgroundImageState.shouldCheckImageAvailability) {
            setBackgroundImageState({
                currentBackgroundImageSrc: nextBackgroundImageSrc,
                shouldCheckImageAvailability: shouldCheckImageAvailability,
            })
        }
    }
    const checkImageAvailability = (qualityOptions: QualityOptions) => {
        const { background } = props
        if (!background || !BackgroundImage.isImageObject(background)) return

        const image = new Image()
        image.src = imageUrlForFill(background, qualityOptions)
        image.onerror = () => {
            if (!ref || !ref.current) return
            ref.current.style.backgroundImage = imageUrlForFill(background)
        }
    }

    collectBackgroundImageFromProps(
        props,
        rect,
        style,
        backgroundImageState.shouldCheckImageAvailability,
        checkImageAvailability
    )
    return style
}

export type MotionDivProps = HTMLMotionProps<"div">

/**
 * @internalremark do no use separately from FrameProps
 * @public
 * */
export interface LayoutProperties {
    /**
     * Distance from the top in pixels. Set to `0` by default.
     * @remarks
     * ```jsx
     * <Frame top={100} />
     * ```
     * @public
     */
    top: number | string | MotionValue<number | string>
    /**
     * Distance from the right in pixels. Set to `0` by default.
     * @remarks
     * ```jsx
     * <Frame right={100} />
     * ```
     * @public
     */
    right: number | string | MotionValue<number | string>
    /**
     * Distance from the bottom in pixels. Set to `0` by default.
     * @remarks
     * ```jsx
     * <Frame bottom={100} />
     * ```
     * @public
     */
    bottom: number | string | MotionValue<number | string>
    /**
     * Distance from the left in pixels. Set to `0` by default.
     * @remarks
     * ```jsx
     * <Frame left={100} />
     * ```
     * @public
     */
    left: number | string | MotionValue<number | string>
    /**
     * Set the CSS `width` property. Set to `200` by default. Accepts all CSS value types (including pixels, percentages, keywords and more).
     * @remarks
     * ```jsx
     * // Pixels
     * <Frame width={100} />
     *
     * // Percentages
     * <Frame width={"100%"} />
     * ```
     * @public
     */
    width: number | string | MotionValue<number | string>
    /**
     * Set the CSS `height` property. Set to `200` by default. Accepts all CSS value types (including pixels, percentages, keywords and more).
     * @remarks
     * ```jsx
     * // Pixels
     * <Frame height={100} />
     *
     * // Percentages
     * <Frame height={"100%"} />
     *
     * ```
     * @public
     */
    height: number | string | MotionValue<number | string>
    /**
     * Set the CSS `position` property. Set to `"absolute"` by default.
     * @remarks
     * ```jsx
     * <Frame position={"relative"} />
     * ```
     * @public
     */
    position: React.CSSProperties["position"]
}

/**
 * @internalremark do no use separately from FrameProps
 * @public
 * */
export interface VisualProperties {
    /**
     * Defines whether or not the `Frame` is visible. Unlike `opacity`, this property cannot be animated. Set to `true` by default. Maps to CSS.
     * @remarks
     * ```jsx
     * <Frame visible={false} />
     * ```
     * @public
     */
    visible: boolean
    /**
     * Set the opacity value, which allows you to make elements semi-transparent or entirely hidden. Useful for show-and-hide animations.
     * Set to `1` by default.
     * @remarks
     * ```jsx
     * <Frame opacity={0.5} />
     * ```
     * @public
     */
    opacity: number | MotionValue<number>
    /**
     * Set the CSS border property, which accepts width, style and color.
     * Set to `"none"` by default.
     * @remarks
     * ```jsx
     * <Frame border="1px solid #09F" />
     * ```
     * @public
     */
    border: string | MotionValue<string>
    /**
     * Set the CSS border-radius property, in pixels or percentages.
     * Set to `0` by default.
     * @remarks
     * ```jsx
     * // Radius with pixels
     * <Frame radius={10} />
     *
     * // Radius with percentages
     * <Frame radius="50%" />
     * ```
     * @public
     */
    radius: number | string | MotionValue<number | string>
    /**
     * Set the CSS border-radius property, in pixels or percentages. Alias for `radius`
     * Set to `0` by default.
     * @remarks
     * ```jsx
     * // Radius with pixels
     * <Frame borderRadius={10} />
     *
     * // Radius with percentages
     * <Frame borderRadius="50%" />
     * ```
     * @public
     */
    borderRadius: number | string | MotionValue<number | string>
    /**
     * Set the color for text elements inside of a `Frame`. By default, text within Frames will be rendered in black.
     * @remarks
     * ```jsx
     * <Frame color="#09F" />
     * ```
     * @public
     */
    color: string | MotionValue<string>
    /**
     * Set the CSS overflow property. Set to `"visible"` by default.
     * @remarks
     * ```jsx
     * <Frame overflow="hidden" />
     * ```
     * @public
     */
    overflow: "visible" | "hidden"
    /**
     * Set the CSS box-shadow property.
     * @remarks
     * ```jsx
     * <Frame shadow="10px 5px 5px black" />
     * ```
     * @public
     */
    shadow: string | MotionValue<string>

    /**
     * Position the children of the frame in 3D space. Set to `false` by default.
     * @remarks
     * ```jsx
     * <Frame preserve3d={true} />
     * ```
     * @beta
     */
    preserve3d: boolean

    /**
     * Sets whether the back face is visible when turned towards the user. Set to `true` by default.
     * @remarks
     * ```jsx
     * <Frame backfaceVisible={true} />
     * ```
     * @beta
     */
    backfaceVisible: boolean
}

/**
 * @internalremark do no use separately from FrameProps
 * @public
 * */
export interface ShortcutsProperties {
    /**
     * Shortcut for passing an image to the `background` property.
     * @remarks
     * ```jsx
     * <Frame image="https://source.unsplash.com/random" />
     * ```
     * @public
     */
    image: string
    /**
     * Shortcut for centering Frames.
     * @remarks
     * ```jsx
     * // Center
     * <Frame center />
     *
     * // Center horizontally
     * <Frame center="x" />
     *
     * // Center vertically
     * <Frame center="y" />
     * ```
     * @public
     */
    center: boolean | "x" | "y"
    /**
     * Shortcut for setting the width and height simultaneously.
     * @remarks
     * ```jsx
     * <Frame size={100} />
     * ```
     * @public
     */
    size: Size | number | string
}

