import { Gradient, LinearGradient, RadialGradient } from "../types/Gradient"
import { Animatable } from "../../animation/Animatable"
import { isMotionValue } from "../../render/utils/isMotionValue"
import { Color } from "../types/Color"
import { BackgroundImage } from "../types/BackgroundImage"
import { MotionStyle, MotionValue } from "framer-motion"

export type Background = Color | Gradient | BackgroundImage | MotionValue<string> | string

export interface DeprecatedBackgroundProperties {
    background: Animatable<Background> | Background | null
}

/** @public */
export interface BackgroundProperties {
    /**
     * Set the background of a `Frame`. Supports color strings, color objects and images by using `src`. Set to a semi-transparent blue color by default.
     * ```jsx
     * <Frame background="#09F"/>
     * <Frame background={Color({r: 255, g: 0, b: 102})} />
     * <Frame background={{ alpha: 1, angle: 75, start: "#09F", end: "#F09"}} />
     * <Frame background={{ src: "https://example.com/logo.png"}} />
     * ```
     * @public
     */
    background: Background | null
}

// Note: this does not include background images
export function collectBackgroundFromProps(
    props: Partial<DeprecatedBackgroundProperties | BackgroundProperties>,
    style: MotionStyle
) {
    const background = Animatable.get(props.background, null)
    if (typeof background === "string" || isMotionValue(background)) {
        style.background = background
    } else if (LinearGradient.isLinearGradient(background)) {
        style.background = LinearGradient.toCSS(background)
    } else if (RadialGradient.isRadialGradient(background)) {
        style.background = RadialGradient.toCSS(background)
    } else if (Color.isColorObject(background)) {
        style.backgroundColor = background.initialValue || Color.toRgbString(background)
    }
}
